
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
h1, h2 {
    color: #ffffff;
}
h1.title{
  color: #ffffff;

}
h3.time {
    margin-top: 0;
    margin-bottom: 0.5em;
    color: #daf6ff;
    font-weight: 500;
    background-color: #096dd9;
    margin-bottom: 0px;
    font-family: 'Share Tech Mono', monospace;
    text-shadow: 0 0 20px #0aafe6, 0 0 20px rgba(10, 175, 230, 0);
}
p {
    margin-top: 2em; 
    margin-bottom: 1em;
    margin: 0;
    padding: 0;
    font-size: 1.5em;
}
html, body {
  height: 100%;
}
body {
  background: #0f3854;
  background: radial-gradient(ellipse at center, #0a2e38 0%, #000000 70%);
  background-size: 100%;
}
* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
*:before, *:after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
body {
  line-height: 1;
  font-family: Lato, sans-serif;
  background-color: #1b1b1c;
  font-size: 100%;
  vertical-align: baseline;
}
.grid-container {
  padding: 0;
  margin: 0;
  list-style: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.grid-item {
  width: 210px;
  margin: 10px;
  background-color: #3c3b3d;
  overflow: hidden;
}
header {
  margin: 20px 0;
}
header h1 {
  font-weight: 600;
  font-size: 1.4rem;
  color: #ffffff;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.1em;
}
.card {
  position: relative;
  width: 100%;
  padding: 6.5em 1em;
  display: block;
}
.card:after {
  position: absolute;
  content: "";
  top: 0;
  right: 0;
  height: 100%;
  width: 50%;
}
.card-night {
  background-color: #000;
}
.card-rain {
  background-color: #41ba9c;
}
.card-snow {
  background-color: #ccd1d9;
}
.card-storm {
  background-color: #656d78;
}
.card-sunny {
  background-color: #4fc1e9;
}
.status {
  color: #808080;
  text-align: center;
}
.status p:first-child {
  font-weight: 600;
  color: #FFF;
}
.night {
  -webkit-animation: night-stars 5s ease-in-out infinite;
  animation: night-stars 5s ease-in-out infinite;
  background-color: #ffffff;
  border-radius: 50%;
